body {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  #calculator {
    font-family: Arial, sans-serif;
    background-color: hsl(0, 3%, 15%);
    width: 100%;
    max-width: 500px;
    border-radius: 10%;
    padding: 15px;
    overflow: hidden;
  }
  
  #display {
    padding: 9px 10px;
    font-size: 5rem;
    text-align: left;
    border: none;
    background-color: hsl(0, 0%, 20%);
    color: white;
  }
  
  .reset-btn {
    width: 150px;
    height: 100px;
    font-size: 2rem;
    background-color: hsl(28, 86%, 49%);
  }
  
  #keys {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    padding: 15px;
  }
  
  #reset-btn {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 20px;
  }
  
  button {
    width: 100px;
    aspect-ratio: 1;
    height: 100px;
    border-radius: 60px;
    border: none;
    background-color: hsl(0, 0%, 20%);
    color: white;
    font-size: 3rem;
    font-weight: bolder;
    cursor: pointer;
  }
  
  button:hover {
    background-color: hsl(0, 0%, 50%);
  }
  
  .operator-btn {
    background-color: hsl(28, 86%, 49%);
  }
  
  .operator-btn:hover {
    background-color: hsl(28, 100%, 70%);
  }
  
  /* Media Queries for Responsiveness */

  
  
  /* Medium screens */
  @media (max-width: 768px) {
    #calculator {
      max-width: 500px;
    }
  
    #display {
      font-size: 3rem;
      padding: 10px 10px;
    }
  
    button {
      font-size: 2rem;
      height: 80px;
      width: 80px;
    }
  
    .reset-btn {
      font-size: 1.2rem;
    }
  
    #keys {
      grid-template-columns: repeat(3, 1fr); /* 3 buttons per row on medium screens */
    }
  
    #reset-btn {
      font-size: 1.2rem;
    }
  }
  
  /* Small screens */
  @media (max-width: 680px) {
    #calculator {
      max-width: 100%;
    }
  
    #display {
      font-size: 2rem;
      padding: 10px 15px;
    }
  
    button {
      font-size: 1.5rem;
      height: 70px;
      width: 70px;
      margin: 0; 
    }
  
    .reset-btn {
      font-size: 1rem;
      width: 70px;
    }
    #keys {
      gap: 2px;
      grid-template-columns: repeat(3, 1fr); /* 3 buttons per row */
      padding: 0; /* Remove any padding */
    }
  }
    
  
  /* Very small screens */
 /* Very small screens (max-width: 390px) */
 @media (max-width: 390px) {
  #calculator {
    max-width: 100%;
    padding: 10px;
    border-radius: 0;
  }

  #display {
    font-size: 1.5rem;
    padding: 10px;
    text-align: left;
  }

  button {
    font-size: 1.2rem;
    width: 60px;
    height: 60px;
    margin: 0; /* Ensure there's no margin between buttons */
  }

  .reset-btn {
    font-size: 1rem;
    width: 80px;
  }

  #keys {
    display: grid; /* Ensure grid layout */
    gap: 0; /* Remove any gap between buttons */
    grid-template-columns: repeat(2, 1fr); /* 2 buttons per row */
    margin: 0; /* Reset margin if any */
    padding: 0; /* Reset padding if any */
  }

  /* Optional: Remove padding or margins on buttons (to ensure alignment) */
  button {
    padding: 0; /* Ensure no padding within buttons */
  }
}
